import * as React from 'react';
import './about.scss';
import {withNamespaces} from "react-i18next";
interface Props {
    t: any
}
/**
 * About page
 */
class About extends React.Component<Props> {

  componentDidMount() {
    About.setMeta();
  }

  /**
   * @return {string} html
   */
  render() {
    const {t} = this.props
    return (
      <h3>
        <article>
            <i>
            {t('   ,        -      !         .          .')}
            </i>
            </article>
      </h3>
    );
  }

  static setMeta() {
    document.title = 'About';
  }
}

// @ts-ignore
export default withNamespaces()(About);
